<template>
  <div id="home">
    <NavBar class="home_bar">
      <div slot="nav_center">购物街</div>
    </NavBar>
    <TabControl
        :tabNums="tabNums"
        @tabcontrol="tabcontrol"
        ref="tabControl_hide"
        v-show="ishome_tab_show"
        :class="{home_tab_control: ishome_tab_show}"
      ></TabControl>

    <Scroll
      class="content"
      ref="scroll"
      :probe-type="3"
      @scrollPositio="scrollPositio"
      :pull-up-load="true"
      @pullingUp="pullingUp"
    >
      <HomeSwiper :banners="banners" @swiperImgLoad="swiperImgLoad"></HomeSwiper>
      <FetureView :recommend="recommend"></FetureView>
      <WeekPop></WeekPop>
      <TabControl
        :tabNums="tabNums"
        @tabcontrol="tabcontrol"
        ref="tabControl"
      ></TabControl>
      <GoodsListShow :goods="goods[currentType].list" />
    </Scroll>
    <BackTop @click.native="backtop" v-show="isShowBackTop"></BackTop>
  </div>
</template>

<script>
import NavBar from "@/components/navBar/common/NavBar";
import TabControl from "@/components/tabControl/context/TabControl";
import GoodsListShow from "@/components/goodslist/GoodsListShow";
import Scroll from "@/components/scroll/Scroll";
import BackTop from "@/components/backtop/BackTop";

import HomeSwiper from "./childrenComps/HomeSwiper";
import FetureView from "./childrenComps/FetureView";
import WeekPop from "./childrenComps/WeekPop";

import {itemListenMixin} from "@/until/mixin.js"

import { getHomeMultiData, getHomeData } from "@/network/home";
export default {
  components: {
    NavBar,
    HomeSwiper,
    FetureView,
    WeekPop,
    TabControl,
    GoodsListShow,
    Scroll,
    BackTop,
  },
  mixins: [itemListenMixin],
  data() {
    return {
      banners: [],
      recommend: [],
      tabNums: ["流行", "新款", "精选"],
      goods: {
        //多重数据整合
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] },
      },
      currentType: "pop", //根据tab栏点击的不同进行向子组件传对应的值
      isShowBackTop: false, //显示回到顶部按钮
      tabControlOffsetTop: 0, //记录标签栏距离滚动顶部距离
      isShowTabControl: false, //标签栏特殊样式
      ishome_tab_show: false,
      saveY: 0, //路由跳转记录位置

    };
  },
  created() {
    getHomeMultiData().then((res) => {
      console.log(res);
      this.banners = res.data.banner.list;
      this.recommend = res.data.recommend.list;
    });
    //分别获取数据
    this.getHomeData("pop");
    this.getHomeData("new");
    this.getHomeData("sell");
  },
  mounted() {
    console.log(this.$refs.scroll.scroll);
    
    console.log(this.isShowTabControl2)
      
  },
  activated(){ //组件进来
    this.$refs.scroll.scrollTop(0,this.saveY)
    this.$refs.scroll.refresh()
  },
  deactivated(){ //组件离开
    //保存滚动距离 y值
    this.saveY = this.$refs.scroll.getScrollY()

    //取消全局事件监听
    this.$bus.$off('imgLoad',this.itemImgLoad)
  },
  methods: {
    /**
     * 获取首页多头数据
     */
    getHomeData(type) {
      //分别获取数据
      let page = this.goods[type].page + 1;
      getHomeData(type, page).then((res) => {
        console.log(res.data);
        this.goods[type].list.push(...res.data.list);
        this.goods[type].page += 1;

        /**完成上拉加载 */
        setTimeout(() => {
          this.$refs.scroll.finishPullUp();
        }, 1000);
      });
    },
    /**
     * 事件监听
     */
    tabcontrol(index) {
      //tab栏点击，实现数据变化
      console.log(index);
      switch (index) {
        case 0:
          this.currentType = "pop";
          break;
        case 1:
          this.currentType = "new";
          break;
        case 2:
          this.currentType = "sell";
          break;
      }
      /**隐藏的标签栏和显示标签栏保存一致状态 */
      this.$refs.tabControl_hide.currentIndex = index
      this.$refs.tabControl.currentIndex = index
    },
    swiperImgLoad(){
      console.log('轮播图图片加载完')
      this.tabControlOffsetTop = this.$refs.tabControl.$el.offsetTop
      console.log(this.$refs.tabControl.$el.offsetTop)
    },
    backtop() {
      //回到顶部
      this.$refs.scroll.scrollTop(0, 0, 300);
    },
    scrollPositio(position) {
      //监听到滚动距离
      this.isShowBackTop = Math.abs(position.y) >= 610;
      this.ishome_tab_show = Math.abs(position.y) >= this.tabControlOffsetTop
    },
    pullingUp() {
      //上拉加载更多
      
      this.getHomeData(this.currentType); //现在只能加载一次，需要处理
    },

    
  },
};
</script>

<style scoped>
#home {
  padding-top: 44px;
  position: relative;
  height: 100vh;
}
.home_bar {
  text-align: center;
  background: pink;
  color: #fff;
  letter-spacing: 2px;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 10;
}
/* .home_tab_control{
  position: relative;
  left: 0;
  right: 0;
  top: 44px;
} */
.content {
  overflow: hidden;
  height: calc(100% - 93px);
}
</style>