<template>
  <Swiper>
    <SwiperItem v-for="(item,index) in banners" :key="index">
      <a>
        <img :src="item.image" alt="" @load="imgLoad">
      </a>
    </SwiperItem>
  </Swiper>
</template>

<script>
import Swiper from '@/components/swiper/common/Swiper'
import SwiperItem from '@/components/swiper/common/SwiperItem'
export default {
  props:{
    banners:{
      type: Array,
      default(){
        return []
      }
    }
  },
  data(){
    return{
      isLoad: false
    }
  },
  components:{
    Swiper,
    SwiperItem
  },
  methods:{
    imgLoad(){
      if(!this.isLoad){
        this.$emit('swiperImgLoad')
        this.isLoad = true
      }
      
    }
  }
}
</script>

